* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	width: 100%;
	height: auto;
	background-color: rgb(223, 223, 223);
}

.App {
	width: 100%;
	height: auto;
}

ul {
	padding-left: 20px;
}


.profil {
	width: 50%;
	object-fit: contain;
	max-width: 400px;
	transform: translate(50%, 10%);
}

.logo-project {
	width: 60%;
	max-width: 600px;
	object-fit: contain;
	opacity: 0;
}

.slideY-one {
	animation: slideY 1s ease-out 0.4s forwards;
}

.slideY-two {
	animation: slideY 1s ease-out 0.8s forwards;
}

.slideY-three {
	animation: slideY 1s ease-out 1.2s forwards;
}

@keyframes slideY {
	100% {
		opacity: 1;
		transform: translateY(-10px);
	}
}

.illustration {
	width: 100%;
	border-radius: 12px;
}

.react {
	width: 20px;
	height: 20px;
	animation: parade 4s linear;
	position: absolute;
	opacity: 0;
}

@keyframes parade {
	0% {
		transform: scale(0);
	}

	30% {
		opacity: 0.5;
	}

	90% {
		transform: scale(1.2) rotateZ(3600deg);
		opacity: 0.7;
	}

	100% {
		transform: scale(1) rotateZ(3600deg);
		opacity: 0;
	}
}

.bottomNav {
	background-color: rgb(223, 223, 223);
	width: 100%;
	position: fixed;
	bottom: 0;
	left: 50%;
	transform: translateX(-50%);
}

.modalMenu {
	background-color: #004ba0;
	height: 100%;
	position: fixed;
	width: 0;
	top: 0;
	right: 0;
	opacity: 0;
	z-index: 1000;
	animation: curtain 0.2s linear forwards;
	padding-top: 20px;
	max-width: 600px;
}

@keyframes curtain {
	80% {
		width: 100%;
	}
	100% {
		width: 100%;
		opacity: 1;
	}
}

.openProject {
	position: absolute;
	top: 0%;
	right: 0%;
	width: 100%;
	height: 100%;
	background-color: white;
	transform: scale(1);
	transition: 0.3s;
	padding: 60px 0;
}

.closeProject {
	position: absolute;
	top: 0%;
	right: 0%;
	width: 100%;
	height: 100%;
	transform: scale(0);
	transition: 0.2s;
	opacity: 0;
}

.rotate {
	animation: rotation 4s linear infinite;
}

@keyframes rotation {
	100% {
		transform: rotateZ(120deg);
	}
}

.logoCard {
	width: 100%;
	opacity: 0.1;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}


.picture-about {
	width: 70%;
	opacity: 0;
}

@media screen and (min-width: 600px) {
	.title,
	.description {
		padding: 25px 30px;
	}

	.profil {
		transform: translate(0%, 10%);
	}
}

@media screen and (min-width: 900px) {
	.illustration {
		width: 80%;
	}
	.container-illustration {
		background-color: #5da0c1;
	}
}

.container-illustration {
	opacity: 0;
	border-radius: 12px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
